@import (less) "color.less";

.info-client-container {
    display: table-row;
    height: 50px;
    color: @white;
}

.icon-client-container,
.data-client-container {
    display: table-cell;
    vertical-align: middle;
    padding-left: 5px;
    padding-right: 5px; 
}

.icon-client {
    display: inline-block;
    width: 32px; //2em; per dimensione 16px;
    height: 32px;
    font-size: 32px;
}